<template>
    <el-container>
        <el-header>
            <container-top></container-top>
        </el-header>
        <el-container>
            <nav-header></nav-header>
            <el-main>
                <el-container class="container-main-div">
                    <div class="container-div">
                        <div class="container-main">
                            <div class="top-main">
                                <div class="page-top-line">
                                    <div class="page-title">数据字典</div>
                                </div>
                                <div class="screen-block">
                                    <el-button class="submit-btn-style" @click="handleAdd">更新数据字典</el-button>
                                </div>
                            </div>
                            <div class="out-div">
                                <div class="datadic-list">
                                    <template>
                                        <el-table :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%">
                                            <el-table-column prop="attrName" label="数据项名"></el-table-column>
                                            <el-table-column prop="describe" label="含义说明"></el-table-column>
                                            <el-table-column prop="cnName" label="中文名"></el-table-column>
                                            <el-table-column prop="dataType" label="数据类型"></el-table-column>
                                            <el-table-column prop="class" label="类别"></el-table-column>
                                            <el-table-column prop="area" label="取值范围"></el-table-column>
                                            <el-table-column prop="provider" label="提供"></el-table-column>
                                        </el-table>
                                    </template>
                                </div>
                            </div>
                        </div>
                    </div>
                </el-container>
                <el-dialog title="更新数据字典" :visible.sync="dialogVisibleAdd" width="500px" :lock-scroll="false" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
                    <el-form ref="formAdd" :model="formAdd" label-width="120px">
                        <el-form-item label="文件名" style="display:none">
                            <el-input v-model="formAdd.filename"></el-input>
                        </el-form-item>
                        <el-form-item label="上传CSV文件" style="width: 400px;">
                            <!-- <div class="img-div">
                                <p>选择文件</p> -->
                                <el-input v-model="formAdd.file" class="add-uploadimg" type="file"></el-input>
                            <!-- </div> -->
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button class="submit-btn-style" type="primary" @click="attributeUpdate">确 定</el-button>
                        <el-button class="cancle-btn-style" @click="dialogVisibleAdd = false">取 消</el-button>
                    </span>
                </el-dialog>
            </el-main>
        </el-container>
    </el-container>
</template>
<style>
    @import "../../static/css/dataDic.css";
</style>
<script>
import $ from "jquery";
import Header from "../sub/aside";
import containerTop from "../sub/container_top";
export default {
    name: "dataDic",
    components: {
        "nav-header": Header,
        "container-top": containerTop,
    },
    data: function() {
        return {
            username:Cookies.get("fedname"),
            token:Cookies.get("fedtoken"),
            tableData: [],
            pagesize:10,
            currentPage:1,
            // 创建账号弹窗
            dialogVisibleAdd:false,
            formAdd:{},
            fileList:[],
        }
    },
    mounted(){
        this.findDimensions()
        this.attributeReq()
    },
    methods: {
        //数据字典查询
        attributeReq:function(){
            var that = this;
            that.axios.post('/attributeReq',that.qs.stringify({
                "specific":true,
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    that.tableData=response.data.attributes;
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },
        handleAdd:function(){
            var that=this;
            that.dialogVisibleAdd=true;
        },
        // 数据字典更新
        attributeUpdate:function(){
            var that=this;
            if(that.formAdd.file==""){
                that.$message.error('请上传CSV文件');
            }
            else {
                var file =  document.querySelector('.add-uploadimg input[type=file]').files[0];
                console.log(file)
                var formData=new FormData()
                formData.append('csv',file)
                formData.append('operator',that.username)
                formData.append('token',that.token)
                that.axios.post('/attributeUpdate',formData,
                {
                    headers:{
                        'Content-Type':'multipart/form-data',
                    }
                },
                {
                    emulateJSON:true
                })
                .then(function(response){
                    that.dialogVisibleAdd=false;
                    if(response.data.message.state){
                        that.$message({
                            message: '数据字典更新成功',
                            type: 'success'
                        });
                        that.attributeReq();
                    }
                    else {
                        that.$message.error(response.data.message);
                    }
                },function(error){
                    that.$message.error(error.response.data.message);
                    if(error.response.status==401){
                        that.$router.push('/');
                        Cookies.set("v2tokens",1,{expires:-1});
                        Cookies.set("v2name",1,{expires:-1});
                        Cookies.set("v2role",1,{expires:-1});
                        Cookies.set("v2time",1,{expires:-1});
                    }
                })
            }
        },
        findDimensions:function(){
            if(window.innerWidth)
            this.winWidth = window.innerWidth;
            else if ((document.body) && (document.body.clientWidth))
            this.winWidth = document.body.clientWidth;
            //获取窗口高度
            if(window.innerHeight)
            this.winHeight = window.innerHeight;
            else if((document.body) && (document.body.clientHeight))
            this.winHeight = document.body.clientHeight;
            //通过深入Document内部对body进行检测，获取窗口大小
            if(document.documentElement && document.documentElement.clientHeight && document.documentElement.clientWidth){
                this.winHeight = document.documentElement.clientHeight;
                this.winWidth = document.documentElement.clientWidth;
            }
            $(".datadic-list").attr("style","min-height:"+(this.winHeight-360)+"px;")
        },
    }
};
</script>



